.articleListSmall {
  .articleListSmall__item {
    overflow: hidden;
    @include mobileBorder(); }
  .articleListSmall__image {
    float: left;
    margin-right: 20px;
    @include md {
      float: none; }
    @include lg {
      float: left;
      width: 90px; }
    img {
      width: 90px;
      @include md {
        width: 100%; }
      @include lg {
        width: auto; } } }
  .articleListSmall__headline {
    font-size: 1.75rem;
    font-weight: 700;
    line-height: 115%;
    margin-top: 0px;
    margin-bottom: 10px;
    @include md {
      margin-top: 10px; }
    @include lg {
      margin-top: 0px; }
    &:hover a {
      color: $colorTheme; } } }
