a:hover, a:focus {
  color: inherit;
  text-decoration: none; }

.modal {
  input {
    width: 100%; } }

.modal-header {
  padding: 20px; }

.modal-header .close {
  font-size: 3rem;
  color: #333;
  font-weight: bold;
  position: absolute;
  top: 20px;
  right: 20px;
  opacity: 1; }

.modal-title {
  font-family: $fontSerif;
  font-size: 2rem;
  font-weight: 700;
  margin-top: 0px;
  padding-right: 20px; }

.modal-body {
  padding: 20px; }

.modal-backdrop {
  background-color: rgba(29, 33, 35, 0.8); }

blockquote {
  padding: 0px;
  margin: 0px;
  font-size: 1.8rem;
  border-left: none; }

blockquote footer, blockquote small, blockquote .small {
  display: block;
  font-size: 1.6rem;
  line-height: normal;
  color: $colorWhite; }

blockquote footer:before, blockquote small:before, blockquote .small:before {
  display: none; }
