.sidebarMenu {
  overflow: hidden;
  border-left: 2px solid #e6e6e6;
  padding: 40px 0px 40px 60px; }
.sidebarMenu__headline {
  font-size: 2rem;
  font-weight: 400;
  float: left;
  clear: both;
  margin-top: 0px;
  margin-bottom: 15px;
  a:hover {
    color: #019bce; } }
.sidebarMenu__headline--active {
  border-bottom: 4px solid #00a9e1; }
